.ChatsHeader {
  width: 35%;
  border-right: 1px solid rgba(102, 102, 102, 0.3);
  padding-top: 20px;
}

.Wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 25px;
  padding-right: 25px;
  border-bottom: 1px solid rgba(102, 102, 102, 0.3);
}

.UserImgContainer {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.UserImg {
  object-fit: cover;
  border-radius: 15px;
}

.InputFilterContainer {
  display: flex;
  justify-content: center;

  border: 1px solid rgba(102, 102, 102, 0.5);
  width: 100%;
  border-radius: 20px;
  padding: 0;
  outline: none;
  margin-bottom: 20px;
}
.SearchIcon {
  stroke: none;
  padding-top: 1px;
  margin-left: 15px;
  width: 20px;
}

.InputFilter {
  display: block;
  width: calc(100% - 35px);
  height: 100%;
  border: none;
  padding: 10px 0 10px 10px;
  border-radius: 20px;
  outline: none;
}
.InputFilter::placeholder {
  color: rgb(102, 102, 102);
}

.Line {
  display: block;
  background-color: rgba(102, 102, 102, 0.3);
  width: 100%;
  height: 1px;
  border: none;
  margin: 0;
  margin-bottom: 30px;
}

.ChatsUsers {
  padding-top: 30px;
  padding-left: 25px;
  padding-right: 25px;
}

.Title {
  margin: 0;
  margin-bottom: 40px;
  color: #85bccf;
  font-size: 25px;
  font-weight: 400;
}

.UsersList {
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 30px;
  height: 100%;
  max-height: 550px;
  overflow: auto;
  overscroll-behavior-y: contain;
}
.UsersItem {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  min-width: 100%;
}

.UsersLink {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  min-width: 100%;
}

.UsersImgContainer {
  margin-right: 10px;
}
.UsersImg {
  border-radius: 15px;
}

.UsersDate {
  position: absolute;
  top: 0;
  right: 0;
}
